import List from "@mui/material/List";
import ChatBubble from "../ChatBubble/ChatBubble";
import { Message } from '../ChatBubble/ChatBubble';

import './ChatMessageBox.css'

interface ChatMessageBoxProps {
    messages: Message[],
}

const ChatMessageBox = (props: ChatMessageBoxProps) => {
    return (
        <List className='list'
            sx={{
                height: '100%',
                width: '100%',
                marginTop: 1,
                overflow: 'auto',
            }}
        >
            {[...props.messages].reverse().map((message, index) => <ChatBubble key={index} message={message} />)}
        </List>
    );
};

export default ChatMessageBox;