import Box from '@mui/material/Box';
import FormControl from '@mui/material/FormControl';
import OutlinedInput from '@mui/material/OutlinedInput';
import CircularProgress from '@mui/material/CircularProgress';
import IconButton from '@mui/material/IconButton';
import SendOutlinedIcon from '@mui/icons-material/SendOutlined';

import './ChatTextBar.css';

interface ChatTextBarProps {
    onSend: () => void,
    onSetPrevMessage: () => void,
    onChange: (value: string) => void,
    isOnRequest: boolean,
    value: string,
}

const ChatTextBar = (props: ChatTextBarProps) => {
    const onEnterPress = (k: any) => {
        // console.log('Key pressed:', k.keyCode);
        if (k.keyCode === 13) props.onSend();
        else if (k.keyCode === 38) props.onSetPrevMessage();
    };

    return (
        <Box sx={{
                width: '100%',
                paddingY: 2,
            }}
        >
            <FormControl fullWidth>
                <OutlinedInput autoFocus
                    fullWidth
                    // multiline
                    // maxRows='5'
                    placeholder='Ask something...'
                    endAdornment={
                        <Box sx={{
                                display: 'flex',
                                flexDirection: 'row',
                            }}
                        >
                            <Box sx={{
                                    marginX: 1,
                                    padding: 0,
                                    display: 'flex',
                                    flexDirection: 'column',
                                }}
                            >
                                <p className='input-counter'>{props.value.length}</p>
                                <p className='input-counter'>{props.value.length > 0 ? props.value.trim().split(' ').length : 0}</p>
                            </Box>
                            {
                                props.isOnRequest ?
                                    <CircularProgress size='1.5rem' />
                                :
                                    <IconButton aria-label='Send' onClick={props.onSend}>
                                        <SendOutlinedIcon />
                                    </IconButton>
                            }
                        </Box>
                    }
                    disabled={props.isOnRequest}
                    value={props.value}
                    onChange={q => props.onChange(q.target.value)}
                    onKeyUp={onEnterPress}
                    size='small'
                    sx={{
                        // paddingX: 3,
                        // paddingY: 1,
                        // fontSize: '1.5rem',
                        backgroundColor: '#697382',
                        borderRadius: 10,
                        '.MuiOutlinedInput-notchedOutline': {
                            border: 1,
                            borderColor: '#00ADB5',
                        },
                    }}
                />
            </FormControl>
        </Box>
    );
};

export default ChatTextBar;