import { useState, useEffect } from 'react';
import { Stack, Box, Container } from '@mui/material';

import ChatMessageBox from './ChatMessageBox/ChatMessageBox';
import { Message } from './ChatBubble/ChatBubble';
import ChatTextBar from './ChatTextBar/ChatTextBar';

import RequestCompletion from '../utils/openai';

const welcomeMessage: Message = {
    type: 'announce',
    content: 'Write something in the text box to start.',
    timeStamp: new Date(),
};

const Chat = () => {
    const [messages, setMessages] = useState<Message[]>([welcomeMessage])
    const [onRequest, setOnRequest] = useState(false);
    const [question, setQuestion] = useState('');

    const sendQuestion = () => {
        if (question.trim().length > 0) {
            const newMessage: Message = {
                type: 'request',
                content: question.trim(),
                timeStamp: new Date(),
            }
            setMessages([...messages, newMessage]);
            console.log('Question send:', question);
            setOnRequest(true);
        } else setQuestion('');
    };

    const setPreviousMessage = () => {
        const requests = messages.filter(value => value.type === 'request').reverse();
        if (requests.length > 0) setQuestion(requests[0].content);
    };

    useEffect(() => {
        if (onRequest) {
            const prompt = question;
            setQuestion('');

            const startTime = Date.now();
            RequestCompletion(prompt)
                .then(response => {
                    console.log("OpenAI response:\n\n", response);
                    const newMessage: Message = {
                        type: 'response',
                        content: response,
                        timeStamp: new Date(),
                        timeTaken: Date.now() - startTime,
                    };
                    setMessages([...messages, newMessage]);
                })
                .finally(() => setOnRequest(false));
        }
    }, [onRequest]);

    return (
        <Box sx={{
                height: '100vh',
                backgroundColor: '#222831'
            }}
        >
            <Container sx={{
                    height: '100%',
                    width: '65%',
                    backgroundColor: '#393E46',
                    border: 1,
                    borderRadius: 5,
                    borderColor: '#B2B2B2',
                }}
            >
                <Stack alignItems='center'
                    justifyContent='space-between'
                    sx={{
                        height: '100%',
                        paddingX: 2,
                    }}
                >
                    <ChatMessageBox messages={messages} />
                    <ChatTextBar onSend={sendQuestion}
                        onSetPrevMessage={setPreviousMessage}
                        onChange={setQuestion}
                        isOnRequest={onRequest}
                        value={question}
                    />
                </Stack>
            </Container>
        </Box>
    );
};

export default Chat;